.cardProduct {
  background: #fff;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  padding: 2rem 1rem;
  &__name {
    font-weight: 600;
    font-size: 1.2rem;
    text-align: center;
  }
  &__img {
    margin: 0.5rem 0 1.5rem 0;
  }

  &__info {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__price {
    color: rgb(0, 143, 48);
  }
  &__btn {
    border: none;
    outline: none;
    // background: linear-gradient(to right, #00c6ff, #0072ff);
    background: rgb(0, 143, 48);
    color: #fff;
    padding: 0.5rem 0.3rem;
    border-radius: 16px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 0.8rem;
    cursor: pointer;
    transition: all 0.5s;

    &--inCart {
      background: linear-gradient(to right, #00c6ff, #0072ff);
    }

    &:hover {
      background: linear-gradient(to right, #00c6ff, #0072ff);
      transform: translateY(-3px);
    }
    &:active {
      transform: translateY(-1px);
    }
  }
}
