.header {
  border-bottom: 1px solid #333;
  padding: 0.5rem;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    display: flex;
    align-items: center;
  }
  &__img {
    width: 50px;
    height: 50px;
  }
  &__logo-text {
    font-size: 1.5rem;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.3);
    margin-left: 1rem;
  }

  &__menu {
    height: 0;
    overflow: hidden;
    transition: all 0.3s;
  }
  &__menu-item {
    transition: all 0.3s;
    text-align: center;
    &:hover {
      transform: translateY(-3px);
    }
    &:active {
      transform: translateY(-1px);
    }

    // tymczasowo
    &:last-child:hover {
      .header__link {
        background: inherit;
        -webkit-background-clip: inherit;
        -webkit-text-fill-color: inherit;
      }
    }
  }
  &__link {
    padding: 0.8rem;
    display: inline-block;
    text-align: center;
    font-weight: 600;
    transition: 0.3s all;

    &:hover {
      background: -webkit-linear-gradient(to right, #00c6ff, #0072ff);
      background: linear-gradient(to right, #00c6ff, #0072ff);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &__toggle {
    transition: all 0.3s;

    &:hover {
      transform: translateY(-3px);
    }
    &:active {
      transform: translateY(-1px);
    }
  }

  @media (min-width: 728px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__menu {
      display: flex;
      height: auto !important;
      margin-left: 4rem;
    }
    &__toggle {
      display: none;
    }
  }
}

.cart-icon {
  position: relative;

  &__count {
    position: absolute;
    top: 0;
    right: 0;
    border: 1px solid red;
    color: #fff;
    background: rgb(255, 0, 0);
    border-radius: 50%;
    padding: 5px;
    font-size: 12px;
    line-height: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.showMenu {
  height: 200px;
}
.active:not(:last-of-type) {
  background: -webkit-linear-gradient(to right, #00c6ff, #0072ff);
  background: linear-gradient(to right, #00c6ff, #0072ff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

